  nav .navbar {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: nowrap;
    align-items: center;
    width: 100%;
    padding: 30px 60px;
    background-color:#ffffff;
  }
  
  .navbar .svg {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style-type: none;
    grid-gap: 10px;
    gap: 10px;
    padding: 4px;
    text-decoration: none;
    color: #292929;
  }
  .svg li {
    cursor: pointer;
    font-size: 1.2rem;
    font-weight: 600;
    text-align: center;
    opacity: 0.9;
  }
  .svg:hover {
    transition: all 0.5s ease;
    border-radius: 4px;
    box-shadow: 0 0 5px #ce2829;
  }

